import React, { Component } from 'react';
import axios from "axios";
import './App.css';


import UserForm from "./components/UserForm";

class App extends Component {
  state = {
    quote: null
  }
  getQuote = (e) => {
      axios.get(`https://api.kanye.rest/`)
      .then((res) => {
        const quote = res.data.quote;
        console.log(quote);
        this.setState({ quote });
      })
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Kanye quotes</h1>
        </header>
        <UserForm getQuote={this.getQuote} />
        { this.state.quote ? <p>"{ this.state.quote }"</p> : <p>Click the button for a new quote</p> }
      </div>
    );
  }
};

export default App;



